$mainColor : #313131;
$subColor : #88888888;

@mixin opacity($num) {
  opacity: $num;
}


.fadeIn {
  opacity: 1;
}

.fadeOut {
  opacity: 0;
}

// nav css


.navBox {
  width: 100%;
  height: 48px;
  padding-right: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: fixed;
  top: 0;
  border-bottom: 0.5px solid $subColor;
  backdrop-filter: saturate(180%) blur(20px);
  background-color: rgba(255, 255, 255, 0.72);
}

.logoImg {
  margin-left: auto;
  width: 30px;
  height: 30px;
  background-image: url('../logo.png');
  background-size: contain;
  background-position: center;
  @include opacity(0.6);

  &:hover {
    @extend .fadeIn;
  }
}

.list {
  position: absolute;
  top: 48px;
  right: 80px;
  list-style: none;
  letter-spacing: 1px;
  font-size: 15px;
  transition: opacity 0.5s;
  margin-top: 8px;

  a {
    text-decoration: none;
  }

  li {
    color: $mainColor;
    margin-bottom: 5px;
    padding-bottom: 2px;
    border-bottom: 0.5px solid $subColor;
    @include opacity(0.6);

    &:hover {
      cursor: pointer;
      @extend .fadeIn;
    }
  }
}


@media screen and (max-width : 768px) {
  .navBox {
    padding-right: 40px;
  }

  .list {
    right: 30px;
  }

  .container {
    padding: 0 40px;
  }
}