
$mainColor : #313131;
$subColor : #88888888;

.marginTop {
  margin-top: 195px;
}

.projectContainer {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  border-bottom: 1px solid $subColor;
  padding-bottom: 50px;
  margin-bottom: 80px;
  color: $mainColor;

  img {
    display: block;
    margin: auto;
    width: 100%;
  }
  
}

.AboutProject {
  margin-right: 30px;
  width: 100%;
  max-width: 480px;

  h1 {
    font-size: 38px;
    font-weight: 700;
    margin-top: 0px;
    margin-bottom: 30px;
  }

  h4 {
    margin-top: 30px;
    margin-bottom: 0px;
    font-weight: 500;
  }

  span {
    font-size: 12px;
    opacity: 0.8;
  }
}

.btn {
  width: 83px;
  height: 27px;
  border-radius: 30px;
  border: 1px solid $mainColor;
  background-color: #fff;
  margin-top: 30px;
  margin-right: 12px;
  font-size: 13px;
  

  a {
    color: $mainColor;
    text-decoration: none;
  }

  &:hover {
    background-color: $mainColor;
    transition: all 0.3s;
    a {
      color: #fff;
    }
    
  }
}

@media screen and (max-width : 1054px) {
  .AboutProject {
    max-width: 100%;
    margin-right: 0px;
  }
  .projectContainer {
    display: flex;
    flex-direction: column;

    img {
      margin-bottom: 20px
    }
  }
  .btn {
    font-size: 12px;
  }
};