@import url('https://fonts.googleapis.com/css2?family=Hahmlet:wght@600;700&display=swap');

$mainColor : #313131;
$subColor : #88888888;

.fadeIn {
  opacity: 1;
}

.fadeOut {
  opacity: 0;
}

// about css

.textContainer {
  font-family: 'Hahmlet', serif;
  margin-top: 100px;
  color: $mainColor;
  font-weight: 700;
  font-size: 70px;

  p {
    margin: 0;
  }
}

.keyword {
  margin-top: 50px;
  list-style: none;
  padding-left: 15px;
  font-size: 17px;
  font-weight: 500;

  li {
    margin-bottom: 3px;
    transition: opacity 0.5s;
  }
}

.check {
  margin-right: 10px;
}

.mainText {
  margin-top: 150px;
  line-height : 1.5;
  padding: 0 5px;
  transition: opacity 0.5s;
}

.skills {
  color: $mainColor;

  h1 {
    font-family: 'Hahmlet', serif;
    text-align: left;
    font-size: 20px;
    border-bottom: 1px solid $subColor;
    padding-bottom: 10px;
    margin-bottom: 30px;
    margin-top: 100px;
  }

  .subTitle{
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 14px;
    opacity: 0.8;
  }

  .aboutMe {
    text-align: left;
    line-height : 1.5;
    font-size: 17px;
    font-weight: 300;
    margin-bottom: 28px;
  }

  .col {
    height: 60px;
  }

  p {
    font-size: 17px;
    font-weight: 400;
    margin-bottom: 30px;
  }
}

.skList {
  list-style: none;
  padding-left: 8px;
  margin-top: 10px;
  margin-bottom: 40px;
}




@media screen and (max-width : 768px) {

  //about

  .textContainer {
    font-weight: 700;
    font-size: 30px;
  }

  .keyword {
    font-size: 13px
  }

  .AboutProject {
    h1 {
      font-size: 30px;
    }
  }
}